import {FC, memo} from 'react';

interface Props {};

const NotFound: FC<Props> = (props) => {
    return(
        <>
            <div className="bg-blue-500 h-screen w-screen">
                Sorry, the page you are looking for is not found.
            </div>
        </>
    );
};

NotFound.defaultProps = {};

export default memo(NotFound);