import {FC, InputHTMLAttributes, memo} from 'react';
import { FiLock } from 'react-icons/fi';
import { FiUser } from 'react-icons/fi';

interface Props extends InputHTMLAttributes<HTMLInputElement> {
    touched?: boolean;
    error?: string;
};

const Input: FC<Props> = ({touched, error, className, placeholder,id, ...rest}) => {
    return(
        <>
            <div className="flex items-center border-b pb-3.5 mt-3 hover:border-blue-700">
                {id === "username" ? 
                <FiUser className="text-blue-700 text-2xl"></FiUser> : 
                <FiLock className="text-blue-700 text-2xl"></FiLock>}
                <input {...rest}
                // onChange={(event) => {setData({...data, username:event.target.value});}} 
                placeholder={placeholder} id={id}
                className={"pl-2 text-md tracking-wide outline-none " + className}></input>
            </div>
            {touched && <div className="text-red-600 text-sm">{error}</div>}
        </>
    );
};

Input.defaultProps = {};

export default memo(Input);