import {FC, memo} from 'react';
import { Link } from 'react-router-dom';

interface Props{};

const Record: FC<Props> = (props) => {
    return (
        <>
            <div>
                This is Recordings Page
                <br/>
                <Link to="/dashboard">
                    <span className="text-blue-600">Go to Dashboard</span>
                </Link>
            </div>
        </>
    );
};

Record.defaultProps = {};

export default memo(Record);