import {FC, memo} from 'react';
import { Link } from 'react-router-dom';

interface Props{};

const Signup: FC<Props> = (props) => {
    return (
        <>
            <div>
                This is SignUp Page.
                Already have an account.
                <Link to="/login">
                    <span className="text-blue-600">Click here</span>
                </Link>
            </div>
        </>
    );
};

Signup.defaultProps = {};

export default memo(Signup);